import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HomePage } from '../home/home';

/**
 * Generated class for the ViewItemsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-view-items',
  templateUrl: 'view-items.html',
})
export class ViewItemsPage {
  info:any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    if(this.navParams.get('data')==null){
      this.navCtrl.setRoot(HomePage);
      this.navCtrl.popToRoot();
    }else{
      
      let d =this.navParams.get('data');
      console.log(d);
      this.info=d.data;
 
    }
    
  }

  getSrc(fname){
    return "./business/"+fname;
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad ViewItemsPage');
  }
  openCard(id){
    this.navCtrl.push('ItemInfoPage',{id:id});
  }
}



